import { StatusBar } from "expo-status-bar";
import {
  Pressable,
  SafeAreaView,
  ScrollView,
  StyleSheet,
  Text,
  TextInput,
  View,
} from "react-native";
import { useState, useCallback, useRef } from "react";

export default function App() {
  const [total, setTotal] = useState(30);
  const [isComplete, setIsComplete] = useState(false);

  const categoryValuesRef = useRef<Record<string, number>>({});
  const setValueForCategory = useCallback(
    (name: string, value: number) => {
      const curVal = categoryValuesRef.current[name];
      if (value === curVal) {
        return;
      }
      categoryValuesRef.current[name] = value;
      const newTotal =
        30 +
        Object.values(categoryValuesRef.current).reduce(
          (curr, next) => (curr || 0) + (next || 0)
        );
      setTotal(newTotal);
      setIsComplete(
        Object.values(categoryValuesRef.current).filter((val) => !isNaN(val))
          .length === 7
      );
    },
    [setTotal, categoryValuesRef]
  );

  return (
    <SafeAreaView style={styles.container}>
      <Header total={total} isComplete={isComplete} />
      <StatusBar style="auto" />
      <ScrollView style={{ flexDirection: "column" }}>
        <Category
          name="Fragrance/Aroma"
          setValueForCategory={setValueForCategory}
          categoryValues={categoryValuesRef.current}
        />
        <Category
          name="Flavor"
          setValueForCategory={setValueForCategory}
          categoryValues={categoryValuesRef.current}
        />
        <Category
          name="Aftertaste"
          setValueForCategory={setValueForCategory}
          categoryValues={categoryValuesRef.current}
        />
        <Category
          name="Acidity"
          setValueForCategory={setValueForCategory}
          categoryValues={categoryValuesRef.current}
        />
        <Category
          name="Body"
          setValueForCategory={setValueForCategory}
          categoryValues={categoryValuesRef.current}
        />
        <Category
          name="Balance"
          setValueForCategory={setValueForCategory}
          categoryValues={categoryValuesRef.current}
        />
        {/* <Category name="Uniformity" setValueForCategory={setValueForCategory} />
      <Category name="Clean Cup" setValueForCategory={setValueForCategory} />
      <Category name="Sweetness" setValueForCategory={setValueForCategory} /> */}
        <Text
          style={{
            marginBottom: 8,
            padding: 24,
            backgroundColor: "lightgray",
            fontSize: 18,
            fontWeight: "600",
          }}
        >
          Technical Categories Assumed to Be 30
        </Text>
        <Category
          name="Overall"
          setValueForCategory={setValueForCategory}
          categoryValues={categoryValuesRef.current}
        />
      </ScrollView>
    </SafeAreaView>
  );
}

const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: "#fff",
    alignItems: "stretch",
    justifyContent: "flex-start",
    maxHeight: "100vh",
  },
});

function Header(props: { total: number; isComplete: boolean }) {
  return (
    <View style={headerStyles.headerContainer}>
      <View style={{ flexDirection: "row" }}>
        <Text style={{ marginRight: 12 }}>ID #</Text>
        <TextInput inputMode="numeric" keyboardType="number-pad" />
      </View>
      <Text style={headerStyles.scoreText}>
        Total: {props.total}
        {!props.isComplete && "*"}
      </Text>
    </View>
  );
}

const headerStyles = StyleSheet.create({
  headerContainer: {
    flexDirection: "row",
    justifyContent: "space-between",
    backgroundColor: "darkgray",
    padding: 12,
    marginBottom: 12,
  },
  sampleIdText: {},
  scoreText: {
    fontWeight: "bold",
  },
});

function Category(props: {
  name: string;
  setValueForCategory: (category: string, value: number) => void;
  categoryValues: Readonly<Record<string, number>>;
}) {
  const setScore = useCallback(
    (value: number) => {
      props.setValueForCategory(props.name, value);
    },
    [props.setValueForCategory]
  );
  const score = props.categoryValues[props.name];

  return (
    <View style={categoryStyles.container}>
      <View style={categoryStyles.rowContainer}>
        <Text style={categoryStyles.labelText}>{props.name}</Text>
        <Text style={categoryStyles.scoreText}>{score ?? "0.00"}</Text>
      </View>
      <QualityScorePicker value={score} setScore={setScore} />
    </View>
  );
}

const categoryStyles = StyleSheet.create({
  container: {
    flexDirection: "column",
    backgroundColor: "lightgray",
    marginBottom: 8,
    padding: 24,
  },
  rowContainer: {
    flexDirection: "row",
    marginBottom: 24,
  },
  labelText: {
    fontSize: 18,
    fontWeight: "600",
    flexGrow: 1,
    marginRight: 24,
  },
  scoreText: {
    fontSize: 18,
    fontWeight: "bold",
  },
});

function QualityScorePicker(props: {
  value: number | undefined;
  setScore: (value: number) => void;
}) {
  const [wholeDigit, setWholeDigit] = useState(
    props.value == null ? undefined : Math.floor(props.value)
  );
  const [decimalPart, setDecimalPart] = useState(
    props.value == null ? undefined : props.value % 1
  );

  const setWholeDigitAndUpdateScore = useCallback(
    (value: number) => {
      setWholeDigit(value);
      if (decimalPart == null) {
        setDecimalPart(0);
      }
      props.setScore(value + (decimalPart ?? 0));
    },
    [setWholeDigit, setDecimalPart, decimalPart, props.setScore]
  );

  const setDecimalPartAndUpdateScore = useCallback(
    (value: number) => {
      if (wholeDigit != null) {
        setDecimalPart(value);
        props.setScore(wholeDigit + value);
      }
    },
    [setWholeDigit, wholeDigit, props.setScore]
  );

  return (
    <View style={qualityScoreStyles.container}>
      <View style={qualityScoreStyles.optionRow}>
        <QualityScoreOptionTile
          value={6}
          setValue={setWholeDigitAndUpdateScore}
          isSelected={wholeDigit === 6}
        />
        <QualityScoreOptionTile
          value={7}
          setValue={setWholeDigitAndUpdateScore}
          isSelected={wholeDigit === 7}
        />
        <QualityScoreOptionTile
          value={8}
          setValue={setWholeDigitAndUpdateScore}
          isSelected={wholeDigit === 8}
        />
        <QualityScoreOptionTile
          value={9}
          setValue={setWholeDigitAndUpdateScore}
          isSelected={wholeDigit === 9}
        />
      </View>
      <View style={qualityScoreStyles.optionRow}>
        <QualityScoreOptionTile
          value={0.0}
          label=".00"
          setValue={setDecimalPartAndUpdateScore}
          isSelected={decimalPart === 0}
        />
        <QualityScoreOptionTile
          value={0.25}
          label=".25"
          setValue={setDecimalPartAndUpdateScore}
          isSelected={decimalPart === 0.25}
        />
        <QualityScoreOptionTile
          value={0.5}
          label=".50"
          setValue={setDecimalPartAndUpdateScore}
          isSelected={decimalPart === 0.5}
        />
        <QualityScoreOptionTile
          value={0.75}
          label=".75"
          setValue={setDecimalPartAndUpdateScore}
          isSelected={decimalPart === 0.75}
        />
      </View>
    </View>
  );
}

function QualityScoreOptionTile(props: {
  value: number;
  label?: string | number | undefined;
  setValue: (value: number) => void;
  isSelected: boolean;
}) {
  const value = props.value;
  const label = props.label ?? value.toString();
  const onPress = useCallback(
    () => props.setValue(props.value),
    [props.setValue, props.value]
  );

  return (
    <Pressable
      style={
        props.isSelected
          ? { ...qualityScoreStyles.optionTile, backgroundColor: "cyan" }
          : qualityScoreStyles.optionTile
      }
      onPress={onPress}
    >
      <Text style={qualityScoreStyles.optionTileText}>{label}</Text>
    </Pressable>
  );
}

const qualityScoreStyles = StyleSheet.create({
  container: {
    flexDirection: "column",
    flexGrow: 1,
  },
  optionRow: {
    flexDirection: "row",
    alignItems: "stretch",
  },
  optionTile: {
    margin: 8,
    padding: 12,
    backgroundColor: "white",
    alignItems: "center",
    justifyContent: "center",
    flexGrow: 1,
  },
  optionTileText: {
    fontWeight: "600",
    fontSize: 18,
  },
});
